<template>
  <div>
    <div class="pagebar-title">新增设备(从机设备)</div>

    <div class="module">
      <div class="mtitle">从机设备名(推荐使用品牌 + 设备型号 + 和使用地点组合)</div>
      <el-input v-model="form.name" placeholder="必填"></el-input>
    </div>

    <div class="module" v-if="getcategorylisrcache.selid"><div>当前选择的从机类型: {{getcategorylisrcache.selid}} - {{getcategorylisrcache.selname}}</div></div>
    <div class="module" v-if="!getcategorylisrcache.selid"><div>选择从机类型</div></div>

    <div class="module">
      <template>
        <el-table :data="getcategorylisrform.tableData" style="width: 100%">
          <el-table-column prop="id" label="分类ID" width="180">
          </el-table-column>
          <el-table-column prop="name" label="从机分类名"> </el-table-column>
          <el-table-column
            label="更多"
            fixed="right"
            width="125"
            align="center"
          >
            <template slot-scope="scope">
              <el-button @click="sel_category(scope.row)">选择</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>

      <div class="module">
        <Pagination
          @pagination="getcategoryList()"
          :page.sync="getcategorylisrform.querypage"
          :pageCount.sync="getcategorylisrform.totalPage"
          :total.sync="getcategorylisrform.total"
          v-if="getcategorylisrform.total > 0"
        />
      </div>

    <div class="module">
      <el-button @click="submit()">添加</el-button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { Bodyaddoredit, Categorylist } from "@/api/equipment";
import Pagination from "@/components/Pagination.vue";
export default {
  name: "equipmentAdd",
  data() {
    return {
      drawer: false,
      form: {
        gatewayid: null,
        name: null,
        category_id: null,
      },
      getcategorylisrform: {
        tableData: [], // 列表
        total: 0, // 总条目
        totalPage: 0, // 总页数
        querypage: 1,
      },
      getcategorylisrcache:{
        selname:"",
        selid:""
      }
    };
  },
  components: {
    Pagination,
  },
  methods: {
    sel_category(data) {
      this.$message({
        message: "当前选择" + data.name,
        type: "success",
      });
      this.form.category_id = data.id;
      this.getcategorylisrcache.selname = data.name
      this.getcategorylisrcache.selid = data.id
    },
    getcategoryList() {
      this.$http(Categorylist(this.getcategorylisrform), (res) => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
          this.getcategorylisrform.total = res.data.total;
          this.getcategorylisrform.querypage = res.data.currentPage;
          this.getcategorylisrform.totalPage = res.data.totalPages;
          this.getcategorylisrform.tableData = res.data.result;
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    submit() {
      this.$http(Bodyaddoredit(this.form), (res) => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
  },
  created() {
    this.form.gatewayid = this.$route.query.rowId;
    console.log(this.form.gatewayid);
    this.getcategoryList();
  },
};
</script>
<style lang="scss" scoped>
</style>