import request from '@/utils/request'

export function Bodylist(data) {
    return request({
        url: '/equipment/body/list',
        method: 'post',
        data: data
    })
}

export function Bodyaddoredit(data) {
    return request({
        url: '/equipment/body/addoredit',
        method: 'post',
        data: data
    })
}

export function Query(data) {
    return request({
        url: '/equipment/query',
        method: 'post',
        data: data
    })
}

export function Add(data) {
    return request({
        url: '/equipment/add',
        method: 'post',
        data: data
    })
}

export function Categorylist(data) {
    return request({
        url: '/equipment/category/list',
        method: 'post',
        data: data
    })
}

export function Categoryaddoredit(data) {
    return request({
        url: '/equipment/category/add',
        method: 'post',
        data: data
    })
}

export function Categorymanager(data) {
    return request({
        url: '/equipment/category/manager',
        method: 'post',
        data: data
    })
}
